import React, { useState } from 'react';
import axios from 'axios';
import styled from 'styled-components';
import * as XLSX from 'xlsx';

const Container = styled.div`
  margin: 20px;
  font-family: 'Noto Sans KR', sans-serif;
  text-align: center;
`;

const Title = styled.h1`
  font-size: 24px;
  margin-bottom: 20px;
`;

const FormContainer = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 20px;
`;

const Label = styled.label`
  font-weight: bold;
  margin-bottom: 10px;
`;

const Input = styled.input`
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 300px;
  margin-bottom: 10px;
`;

const Button = styled.button`
  padding: 10px 20px;
  background-color: #000;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
  font-size: 16px;
  margin-top: 10px;

  &:hover {
    background-color: #555;
  }
`;

const ResultContainer = styled.div`
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #f9f9f9;
  text-align: left;
`;

const ResultTitle = styled.h2`
  font-size: 18px;
  margin-bottom: 10px;
`;

const ResultItem = styled.div`
  margin-bottom: 10px;
`;

const Warning = styled.div`
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #ffcc00;
  background-color: #fff8e1;
  color: #e65100;
  border-radius: 5px;
  font-size: 0.9em;
  line-height: 1.5;
`;

const BusinessNumberLookup = () => {
  const [bNo, setBNo] = useState('');
  const [input2, setInput2] = useState('');
  const [result, setResult] = useState(null);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);
  const [bulkResults, setBulkResults] = useState([]);

  const handleSearch = async () => {
    const cleanedBNo = bNo.replace(/-/g, '');

    if (cleanedBNo.trim() === '') {
      alert("사업자번호를 입력하세요.");
      return;
    }

    const data = {
      b_no: [cleanedBNo]
    };

    try {
      setLoading(true);
      setError(null);

      const response = await axios.post(
        'https://api.odcloud.kr/api/nts-businessman/v1/status?serviceKey=CADGVCpJ6S3ugec34rtjEC4Fq1h0t0sbaD%2BchVRlpPGrKdOCDgyGmI0WnIpPQf4d7a4EPfo8FXmTmJqWxPrqrQ%3D%3D&',
        data,
        {
          headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json'
          }
        }
      );

      console.log('API 응답:', response.data);

      if (response.data.status_code !== "OK" || response.data.match_cnt === 0) {
        setError({ msg: "국세청에 등록되지 않은 사업자등록번호입니다." });
      } else {
        const businessData = response.data.data[0];
        if (businessData.b_stt === "국세청에 등록되지 않은 사업자등록번호입니다.") {
          setError({ msg: businessData.b_stt });
          setResult(null);
        } else {
          const formattedResult = {
            b_no: formatBusinessNumber(businessData.b_no),
            b_stt: businessData.b_stt,
            tax_type: businessData.tax_type
          };
          setResult(formattedResult);
          setError(null);
        }
      }
    } catch (err) {
      setError(err.response ? err.response.data : { msg: "API 요청 중 오류가 발생했습니다." });
      setResult(null);
    } finally {
      setLoading(false);
    }
  };

  const formatBusinessNumber = (bNo) => {
    return bNo.replace(/(\d{3})(\d{2})(\d{5})/, '$1-$2-$3');
  };

  const handleBNoChange = (e) => {
    const formattedBNo = e.target.value.replace(/[^0-9-]/g, '');
    setBNo(formattedBNo);
  };

  const handleInput2Change = (e) => {
    setInput2(e.target.value);
  };

  const handleFileUpload = (e) => {
    const file = e.target.files[0];

    if (file) {
      const reader = new FileReader();

      reader.onload = async (event) => {
        const data = new Uint8Array(event.target.result);
        const workbook = XLSX.read(data, { type: 'array' });
        const sheetName = workbook.SheetNames[0];
        const sheet = workbook.Sheets[sheetName];
        const rows = XLSX.utils.sheet_to_json(sheet, { header: 1 });

        const businessNumbers = rows.map(row => {
          if (typeof row[0] === 'string') {
            return row[0].replace(/-/g, ''); // 하이픈 제거
          }
          return null;
        }).filter(bNo => bNo !== null);

        console.log('엑셀에서 추출된 사업자번호:', businessNumbers);

        try {
          setLoading(true);
          setError(null);

          const response = await axios.post(
            'https://api.odcloud.kr/api/nts-businessman/v1/status?serviceKey=CADGVCpJ6S3ugec34rtjEC4Fq1h0t0sbaD%2BchVRlpPGrKdOCDgyGmI0WnIpPQf4d7a4EPfo8FXmTmJqWxPrqrQ%3D%3D&',
            { b_no: businessNumbers },
            {
              headers: {
                'Content-Type': 'application/json',
                'Accept': 'application/json'
              }
            }
          );

          console.log('API 응답:', response.data);

          const formattedResults = response.data.data.map(item => ({
            b_no: formatBusinessNumber(item.b_no),
            b_stt: item.b_stt,
            tax_type: item.tax_type
          }));

          const validResults = formattedResults.filter(item => item.b_stt !== "국세청에 등록되지 않은 사업자등록번호입니다.");

          setBulkResults(validResults);
          if (validResults.length === 0) {
            setError({ msg: "모든 입력된 사업자번호가 유효하지 않습니다." });
          }
        } catch (err) {
          setError(err.response ? err.response.data : { msg: "API 요청 중 오류가 발생했습니다." });
        } finally {
          setLoading(false);
        }
      };

      reader.readAsArrayBuffer(file);
    }
  };

  const handleExcelDownload = () => {
    const wsData = bulkResults.map(result => [
      result.b_no,
      result.b_stt,
      result.tax_type
    ]);

    const ws = XLSX.utils.aoa_to_sheet([['사업자번호', '사업자 상태', '부가가치세 유형'], ...wsData]);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, '사업자번호 조회 결과');
    XLSX.writeFile(wb, '사업자번호_조회_결과.xlsx');
  };

  const downloadExcelTemplate = () => {
    const templateData = [
      ['사업자번호'],
      ['123-45-67890'],
      ['234-56-78901'],
      ['345-67-89012']
      // 이곳에 필요한 사업자번호 예시를 추가할 수 있습니다.
    ];

    const ws = XLSX.utils.aoa_to_sheet(templateData);
    const wb = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, '사업자번호 양식');
    XLSX.writeFile(wb, '사업자번호_양식.xlsx');
  };

  return (
    <Container>
      <Title>사업자번호 조회</Title>
      <FormContainer>
        <Label htmlFor="bNo">사업자번호:</Label>
        <Input
          type="text"
          id="bNo"
          value={bNo}
          onChange={handleBNoChange}
          placeholder="사업자번호를 입력하세요"
        />
        <Button onClick={handleSearch}>조회</Button>
      </FormContainer>

      <Button onClick={downloadExcelTemplate}>엑셀 양식 다운로드</Button>

      {loading && <p>조회 중...</p>}

      {error && (
        <ResultContainer>
          <ResultTitle>오류:</ResultTitle>
          <div style={{ color: 'red' }}>
            <pre>{JSON.stringify(error, null, 2)}</pre>
          </div>
        </ResultContainer>
      )}

      {result && (
        <ResultContainer>
          <ResultTitle>조회 결과:</ResultTitle>
          <ResultItem>
            <Label>사업자번호:</Label>
            <span>{result.b_no}</span>
          </ResultItem>
          <ResultItem>
            <Label>사업자 상태:</Label>
            <span>{result.b_stt}</span>
          </ResultItem>
          <ResultItem>
            <Label>부가가치세 유형:</Label>
            <span>{result.tax_type}</span>
          </ResultItem>

          {result.b_stt === '계속사업자' && (
            <div style={{ marginTop: '20px' }}>
              <Label htmlFor="input2">추가 입력창:</Label>
              <Input
                type="text"
                id="input2"
                value={input2}
                onChange={handleInput2Change}
                placeholder="추가 정보를 입력하세요"
              />
            </div>
          )}
        </ResultContainer>
      )}

      <FormContainer>
        <Label htmlFor="fileUpload">엑셀 파일 업로드:</Label>
        <Input
          type="file"
          id="fileUpload"
          accept=".xlsx, .xls"
          onChange={handleFileUpload}
        />
      </FormContainer>

      {bulkResults.length > 0 && (
        <ResultContainer>
          <ResultTitle>엑셀 조회 결과:</ResultTitle>
          {bulkResults.map((res, index) => (
            <ResultItem key={index}>
              <Label>사업자번호:</Label>
              <span>{res.b_no}</span>
              <br />
              <Label>사업자 상태:</Label>
              <span>{res.b_stt}</span>
              <br />
              <Label>부가가치세 유형:</Label>
              <span>{res.tax_type}</span>
            </ResultItem>
          ))}
        </ResultContainer>
      )}

      <Warning>
        본 서비스는 사업자간 거래 시 불의의 피해를 방지하기 위해 최소한의 정보를 제공하고 있으며, 증빙자료로서의 법적인 효력이 없습니다.<br />
        세무서에서 사업자등록 변경사항(신규등록, 휴·폐업 등)을 처리완료한 경우에만 반영됩니다.
      </Warning>

      <Button onClick={handleExcelDownload}>엑셀로 다운로드</Button>
    </Container>
  );
};

export default BusinessNumberLookup;
